<template>
  <div>
    <router-view class="box"></router-view>

    <span
      class="card"
      style="width: 500px; height: 800px; background-color: #ffffe0;"
      v-if="anotherUser != null"
    >
      <div class="card-image">
        <figure class="image is-4by3">
          <img :src="imgURL + anotherUser.userImage" alt="Placeholder image" />
        </figure>
      </div>

      <div class="card-content">
        <div class="media">
          <div class="media-left"></div>
          <div class="media-content">
            <p class="title is-4" v-text="anotherUser.userId"></p>
            <p class="subtitle is-6" v-text="anotherUser.name"></p>
          </div>
        </div>

        <div class="content">
          이메일 : {{ anotherUser.email }} <br />
          전화번호 : {{ anotherUser.phoneNum }} <br />
          포지션 : {{ anotherUser.position }} <br />
          stack : {{ anotherUser.stack }} <br />
          <b-button type="is-primary is-light" @click="getPortfolio(anotherUser)"
            >포트폴리오 조회</b-button
          >
          &nbsp;

        </div>
      </div>
    </span>


    <span
      class="card"
      style="width: 500px; height: 800px; background-color: #ffffe0;"
      v-else-if="$route.params.userId != null"
    >
      <div class="card-image">
        <figure class="image is-4by3">
          <img :src="imgURL + $route.params.userImage" alt="Placeholder image" />
        </figure>
      </div>

      <div class="card-content">
        <div class="media">
          <div class="media-left"></div>
          <div class="media-content">
            <p class="title is-4" v-text="$route.params.userId"></p>
            <p class="subtitle is-6" v-text="$route.params.name"></p>
          </div>
        </div>

        <div class="content">
          이메일 : {{ $route.params.email }} <br />
          전화번호 : {{ $route.params.phoneNum }} <br />
          포지션 : {{ $route.params.position }} <br />
          stack : {{ $route.params.stack }} <br />
          <b-button type="is-primary is-light" @click="updateUser()"
            >정보수정</b-button
          >
          &nbsp;
          <b-button type="is-danger is-light" @click="deleteUser()"
            >회원탈퇴</b-button
          >
          &nbsp;
          <b-button type="is-primary is-light" @click="getPortfolio($route.params)"
            >포트폴리오 조회</b-button
          >
        </div>
      </div>
    </span>

    <span
      class="card"
      style="width: 500px; height: 800px; background-color: #ffffe0;"
      v-else
    >
      <div class="card-image">
        <figure class="image is-4by3">
          <img :src="imgURL + loginUser.userImage" alt="Placeholder image" />
        </figure>
      </div>

      <div class="card-content">
        <div class="media">
          <div class="media-left"></div>
          <div class="media-content">
            <p class="title is-4" v-text="loginUser.userId"></p>
            <p class="subtitle is-6" v-text="loginUser.name"></p>
          </div>
        </div>

        <div class="content">
          이메일 : {{ loginUser.email }} <br />
          전화번호 : {{ loginUser.phoneNum }} <br />
          포지션 : {{ loginUser.position }} <br />
          stack : {{ loginUser.stack }} <br />
          <b-button type="is-primary is-light" @click="updateUser()"
            >정보수정</b-button
          >
          &nbsp;
          <b-button type="is-danger is-light" @click="deleteUser()"
            >회원탈퇴</b-button
          >
           &nbsp;
          <b-button type="is-primary is-light" @click="getPortfolio(loginUser)"
            >포트폴리오 조회</b-button
          >
        </div>
      </div>
    </span>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
import router from "../../router";
export default {
  data: () => ({}),
  computed: {
    ...mapState(["loginUser", "anotherUser", "imgURL"]),
  },
  methods: {
    updateUser() {
      return this.$router.push({ name: "updateUser" });
    },
    deleteUser() {
      let formData = new FormData();

      formData.append("userId", this.loginUser.userId);
      formData.append("password", this.loginUser.password);
      formData.append("name", this.loginUser.name);
      formData.append("age", this.loginUser.age);
      formData.append("email", this.loginUser.email);
      formData.append("phoneNum", this.loginUser.phonenum);
      formData.append("address", this.loginUser.address);
      formData.append("stack", this.loginUser.stack);
      formData.append("position", this.loginUser.position);
      formData.append("file", this.loginUser.file);

      axios
        .post("http://localhost:80/deleteuser", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(() => {
          sessionStorage.removeItem("user");
          this.loginUser = null;
          console.log("SUCCESS!!");
          router.push({ name: "mypage" });
        })
        .catch(function() {
          console.log("FAILURE!!");
        });
      return this.$router.push({ name: "Home" });
    },
    getPortfolio(user) {
      this.$store.state.anotherUser = user
      return this.$router.push({ name: "getPortfolio" });
    }
  },
};
</script>
<style scoped>
.card {
  position: static;
  border: 1px solid black;
  float: left;
  margin: 1px;
  padding: 130px 50px;
  width: 1000px;
  height: 1000px;
}
.box {
  position: static;
  float: right;
  width: 6  00px;
  height: 600px;
}
</style>
