<template>
  <b-table :data="portfolios" :columns="columns"></b-table>
</template>

<script>
import axios from "axios";
//import router from "../../router";

export default {
  data() {
    return {
      portfolios: [],
      columns: [
        {
          field: "pfSeq",
          label: "번호",
          width: "40",
          numeric: true,
        },
        {
          field: "pfSubtitle",
          label: "제목",
        },
        {
          field: "pfDuration",
          label: "기간",
        },
        {
          field: "pfDescription",
          label: "상세설명",
        },
        {
          field: "participation",
          label: "참여도",
        },
        {
          field: "pfLang",
          label: "언어",
        },
        {
          field: "pfTools",
          label: "Tool",
        },
        {
          field: "pfDbms",
          label: "DB",
        },
        {
          field: "pfLink",
          label: "Link",
        },
        {
          field: "pfCategory",
          label: "Category",
        },

      ],
    };
  },
  methods: {
    getPortfolio() {
      axios
        .get("http://localhost:80/pf/select/id/" + this.$store.state.anotherUser.userId)
        .then((response) => {
          this.portfolios = response.data;
          console.log(this.portfolios)
          console.log("SUCCESS!!");
        })
        .catch(function() {
          console.log("FAILURE!!");
        });
    },
  },
  mounted() {
    this.getPortfolio();
  },
};
</script>
